@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #076585;
  background: #076585;
  background: linear-gradient(to right, #fff, #076585);
}

.wrapper {
  width: 340px;
  height: 540px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgb(247, 248, 248);
}

.screen {
  height: 100px;
  width: 100%;
  margin-bottom: 10px;
  padding: 0 10px;
  background-color: #4357692d;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: rgb(5, 55, 78);
  font-weight: bold;
  box-sizing: border-box;
}

.buttonBox {
  width: 100%;
  height: calc(100% - 110px);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px;
}

button {
  border: none;
  background-color: rgb(19, 110, 138);
  font-size: 24px;
  color: rgb(5, 55, 78);
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
}

button:hover {
  background-color: rgb(49, 134, 160);
}

.equals {
  grid-column: 3/5;
  background-color: rgb(21, 163, 206);
}

.equals:hover {
  background-color: rgb(49, 134, 160);
}

